body {
  margin: 0;
  padding: 0;
  background-color: #F7F7FF;
  font-family: 'Quicksand', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.red {
  color: #D33D3B;
}

.dark {
  color: #495867;
}

.PresetSetter {
  background: #495867;
  color: #BDD5EA;
  position: fixed;
  top: 0;
  left: 0;
  margin: 0 auto;
  width: 50vw;
  height: 20vh;
}

.PresetSetter h1 {
  font-size: 4.2vw;
}

.PresetSetter select {
  font-size: 2vh;
}

.sliders {
  position: fixed;
  top: 20vh;
  height: 80vh;
  width: 100vw;
  overflow-y: scroll;
  padding: 1vh;
  background-color: #F7F7FF;
}

.AdjustmentSlider {
  padding: 10px;
  background: #F7F7FF;
  color: #495867;
}

.AdjustmentSlider input {
  max-width: 100%;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.ProgressTracker {
  position: fixed;
  top: 0;
  margin: 0 auto;
  background-color: #BDD5EA;
  color: #495867;
  width: 50vw;
  left: 50vw;
  height: 20vh;
  font-size: 2.5vw;
}

.ProgressTracker meter {
  width: 100vw;
  margin-top: 0;
  position: fixed;
  top: 20vh;
  right: 0;
}

.ProgressTracker p {
  color: #D33D3B;
}

input.slider {
  width: 100%;
}

.socials {
  position: fixed;
  top: calc(100vh - 60px);
  bottom: 0;
  width: 100%;
  padding: 1.5vh 0;
  background: #495867;
}

.socials a {
  font-size: 4vw;
  padding: 2.5vw;
  color: #fff;
}

.App {
  text-align: center;
}

.App-header {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(5px + 2vmin);
}

.App-link {
  color: #D33D3B;
}

